body {
  background: linear-gradient(100deg, #8e24aa, #a76754, #00acc1) 0 0 / 600% 600%;
  animation: gradientAnimation 20s infinite;
}

@keyframes gradientAnimation {
  0% {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0%;
  }
}

.breed-select {
  max-width: 400px;
  height: 50px;
  margin: 50px auto;
}

.cat-info {
  text-align: center;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.cat-container-info {
  background-color: #f7f7f7;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-top: 10px;
  padding: 10px;
}

.cat-name {
  font-size: 18px;
  font-weight: bold;
}

.country {
  font-size: 16px;
  font-style: italic;
}

.cat-description, .cat-temperament {
  margin-top: 5px;
}

.wikipedia-link {
  color: #007bff;
  margin-top: 5px;
  text-decoration: none;
  display: inline-block;
}

.wikipedia-link:hover, .wikipedia-link:focus {
  color: #0056b3;
  text-decoration: underline;
}

.loader {
  color: #fff;
  width: 1em;
  height: 1em;
  text-indent: -9999em;
  border-radius: 50%;
  font-size: 10px;
  animation: mulShdSpin 1.3s linear infinite;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateZ(0);
}

@keyframes mulShdSpin {
  0%, 100% {
    box-shadow: 0 -3em 0 .2em, 2em -2em 0 0, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em;
  }

  12.5% {
    box-shadow: 0 -3em, 2em -2em 0 .2em, 3em 0, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }

  25% {
    box-shadow: 0 -3em 0 -.5em, 2em -2em, 3em 0 0 .2em, 2em 2em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }

  37.5% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0, 2em 2em 0 .2em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }

  50% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0, 0 3em 0 .2em, -2em 2em, -3em 0 0 -1em, -2em -2em 0 -1em;
