}

.error {
  text-align: center;
  color: #ff3d00;
  background-color: #fff;
  border: 2px solid #ff3d00;
  border-radius: 4px;
  margin-bottom: 20px;
  padding: 10px;
  font-size: 18px;
  font-weight: bold;
  display: none;
}

.error.show {
  display: block;
}

.hidden {
  display: none;
}

.show {
  display: block;
}

